import React from 'react'
import {
    Container,
    Typography,
} from '@mui/material';

const Hero = () => {

    const container = {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        paddingY: '4rem',
        gap: '1rem',
        color: '#FFF'
    }

    return (
        <Container sx={container}>
            <Typography
                variant='h3'
                sx={{ fontWeight: 'bold' }}>
                Encontre a sua prxima estadia
            </Typography>
            <Typography variant='h5'>Pesquise ofertas em hotis, casas e muito mais...</Typography>
        </Container>
    )
}

export default Hero